<template>
    <scroll-view :scroll-y="true" @scrolltolower="scrolltolower" @refresherpulling="refresherpulling"
        :scroll-with-animation="true" :refresher-background="'#ffffff'" :refresher-triggered="isRefresherTriggered"
        :refresher-enabled="true" @scroll="onScroll" :style="{
            'height': 'calc(100vh - ' + (statusBarHeight - (-navHeight)) + 'px - 191rpx)'
        }">

        <hostPost></hostPost>

        <view class="post-list">
            <view class="post-list-content">
                <view v-for="(item, index) in postData.content" class="post-list-content-item">
                    <view style="height: 15rpx;"></view>
                    <user-post :bodyData="item"></user-post>
                </view>

            </view>
            <view style="height: 20rpx;"></view>
            <view v-if="isEnd" style="text-align: center;color:#999;font-size: 28rpx;">
                <text>没有更多了~</text>
            </view>
            <view style="height: 10rpx;"></view>
        </view>

    </scroll-view>
</template>

<script>
import hostPost from './hostPost.vue';

export default {
    components: {
        hostPost
    },

    props: {
        statusBarHeight: {
            type: Number,
            default: 0
        },
        navHeight: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            isRefresherTriggered: false,
            postData: {
                content: []
            },
            isEnd: false,
            isPublishing: false,
            page: 1,
            requestParam: {
                pageNumber: 1,
                sortField: "gmtCreate",
                sortOrderBy: "desc",
                time: 0
            },
            // 请求次数
            requestCount: 0
        };
    },
    // 初始化
    created() {
        // 获取缓存
        // var postContentRecommend = uni.getStorageSync('postContentRecommend');
        // if (postContentRecommend) {
        //     this.postData.content = postContentRecommend;
        // }
        this.getDefault();
        uni.$on("loginSuccess", () => {
            this.requestCount = 0;
            this.requestParam.pageNumber = 1;
            this.postData.content = [];
            this.isRefresherTriggered = false;
            this.isEnd = false;
            this.isPushing = false;
            this.getDefault();
        });
    },
    methods: {
        onScroll(e) {
        },
        refresherpulling() {
            if (this.isRefresherTriggered) {
                return false;
            }
            this.isRefresherTriggered = true;
            setTimeout(() => {
                this.isRefresherTriggered = false;
            }, 1000);
            var id = this.postData.content[0].id;
            this.getAfterById(id);
        },
        scrolltolower() {
            if (this.isEnd) {
                return false;
            }
            this.getBeforeById(this.postData.content[this.postData.content.length - 1].id);
        },
        /**
         * 下拉刷新
         * @param {*} id
         */
        getAfterById(id) {
            this.$kt.request.request({
                // getLessOfRecommend
                api: this.$api.business.ovo.post.getGreaterOfRecommend,
                data: {
                    id: id
                },
                stateSuccess: res => {
                    var content = res.data;
                    if (content == null || content.length == 0) {
                        return false;
                    }
                    // 往上插入
                    for (var i = content.length - 1; i >= 0; i--) {
                        this.postData.content.unshift(content[i]);
                    }
                }
            });
        },
        /**
         * 获取推荐
         */
        getDefault() {
            this.$kt.request.request({
                api: this.$api.business.ovo.post.getDefaultRecommend,
                data: {},
                stateSuccess: res => {
                    if (this.requestCount == 0) {
                        this.postData.content = [];
                    }
                    this.requestCount++;
                    var content = res.data;
                    if (content == null || content.length == 0) {
                        return false;
                    }
                    this.postData.content = content;
                }
            });
        },
        getBeforeById(id) {
            this.$kt.request.request({
                api: this.$api.business.ovo.post.getLessOfRecommend,
                data: {
                    id: id
                },
                stateSuccess: res => {
                    var content = res.data;
                    if (content == null || content.length == 0) {
                        return false;
                    }
                    // 往下插入
                    for (var i = 0; i < content.length; i++) {
                        this.postData.content.push(content[i]);
                    }
                }
            });
        },
    },
}
</script>

<style lang="scss" scoped>
.post-list {
    background-color: #fff;
    overflow: hidden;
    padding: 0 20rpx 0rpx 20rpx;
    box-sizing: border-box;

    .post-list-content {
        font-size: 36rpx;
        vertical-align: bottom;
    }

    .post-list-content-item {
        box-sizing: border-box;

        .post-list-content-item-title {
            font-size: 32rpx;
            font-weight: bold;
            margin-bottom: 20rpx;
        }

        .post-list-content-item-content {
            font-size: 30rpx;
            color: #333333;
            margin-bottom: 20rpx;
            // 文字间距
            letter-spacing: 2rpx;
            margin-bottom: 20rpx;
            margin-top: 20rpx;
        }
    }

}

.post-list-content-item-bottom-1:active {
    opacity: 0.7;
}

.post-list-content-item-bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0rpx;
    margin-bottom: 20rpx;

    .post-list-content-item-bottom-item {
        display: flex;
        align-items: center;
        margin-left: 30rpx;
        font-size: 24rpx;
        color: #333333;

        .post-list-content-item-bottom-item-icon {
            width: 35rpx;
            height: 35rpx;
            margin-right: 10rpx;
        }
    }
}

.post-list-content-item-bottom-item:active {
    opacity: 0.5;
}
</style>